import axios from 'axios'

const REE_OK = 0;

export function get(url){
    return function(params){
        return axios.get(url,{
            params
        }).then((res) => {
            const {errno , data} = res.data;
            if(errno === REE_OK){
                return data;
            }
        }).catch(() => {
            // console.log(e)
        })
    }
}

